.main, .layout-container {
	min-height: 100%;
	height: auto;
	width: 100%;
}

%sc-layout {
	min-height: 100%;
}



.layout-overlay{
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
}

.cms-content ul, .cms-content ol {
	margin-left: 0px;
}

.cms-content ul li {
	list-style: none;
}

.cms-content ol li {
	list-style: decimal;
}
.cms-content {
	h1, h2, h3, h4{
		text-transform: capitalize;
	}
}